.content {
 min-height: calc(100vh - 64px);
 padding: 1.5rem 0;
}
.cards {
 display: flex;
 justify-content: space-evenly;
 flex-wrap: wrap;
 column-gap: 15px;
 row-gap: 15px;
}

.card {
 width: 300px;
}

.card-title {
 font-weight: 600 !important;
}

.card-image {
 & img {
  height: 400px;
 }
}